.header-parent-container{
    /* box-shadow: rgba(58, 58, 56, 0.1) 0px 10px 10px -10px; */
    display: flex;
    background-color: rgb(94, 95, 95);
    align-items: center;
    justify-content: space-between;
    padding: 8px 16px;
   
    color: rgb(91, 99, 247);

    a{
        
        color: rgb(223, 220, 220);
        text-decoration: none;
    }

    .left{
        display: flex;
        align-items: center;
        gap: 20px;
       
    }

    .right{
        height: 50px;
        width: 50px;
        border-radius: 50%;
        background-color: burlywood;
        
    }
}